import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { withRouter } from 'react-router-dom'
import * as CONSTANT from '../../../../constant'
import formatDistance from 'date-fns/formatDistance'

import Card from '@material-ui/core/Card'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import Toolbar from '@material-ui/core/Toolbar'
import CircularProgress from '@material-ui/core/CircularProgress'

import FavoriteIcon from '@material-ui/icons/Favorite'
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder'
import StarIcon from '@material-ui/icons/Star'
import StarBorderIcon from '@material-ui/icons/StarBorder'

import AppImageLoader from '../../AppImageLoader/AppImageLoader'

const styles = theme => ({
  root: {
    display: 'flex',
    height: '30vh',
    paddingRight: '.5rem',
    cursor: 'pointer'
  },
  cover: {
    width: 170,
    height: '100%'
  },
  cardContent: {
    width: '100%',
    paddingLeft: '1rem'
  },
  comicTitle: {
    width: '100%'
  },
  rateIcon: {
    fontSize: '1rem',
    color: 'orange'
  },
  favoriteIcon: {
    color: 'red'
  }
})

const ListCard = (props) => {
  const { classes, comic, onToggleFavorite, setSelectComic, history } = props
  const {
    title,
    rate,
    isFavorite,
    chapterList,
    timestamp,
    resizeCover,
    isLoading,
    author
  } = comic
  const lastChapter = chapterList[chapterList.length - 1]

  return (
    <div>
      <Card
        raised
        className={classes.root}
        onClick={() => {
          setSelectComic(comic)
          history.push(`/detail/${comic.key}`)
        }}
        style={
          comic.history
            ? {maxHeight: '200px'}
            : {maxHeight: '170px'}
        }>
        <div className={classes.cover}>
          <AppImageLoader
            src={resizeCover}
            alt={title}
            loaderSize={40}
            backgroundSize='cover' />
        </div>
        <div className={classes.cardContent}>
          <Toolbar disableGutters>
            <Typography variant='title' color='inherit' className={classes.comicTitle} noWrap>
              {title}<br />
              {[...Array(rate)].map((item, index) => (
                <StarIcon key={index} className={classes.rateIcon} />
              ))}
              {[...Array(CONSTANT.RATE_TOTAL - rate)].map((item, index) => (
                <StarBorderIcon key={index} className={classes.rateIcon} />
              ))}
            </Typography>
            <IconButton disabled={isLoading} onClick={(event) => onToggleFavorite(event, comic)}>
              {
                isLoading
                  ? <CircularProgress size={30} />
                  : (
                    isFavorite
                      ? <FavoriteIcon className={classes.favoriteIcon} />
                      : <FavoriteBorderIcon className={classes.favoriteIcon} />
                  )
              }
            </IconButton>
          </Toolbar>
          <Typography variant='caption' gutterBottom>
            Author
          </Typography>
          <Typography variant='body1' gutterBottom>
            {author}
          </Typography>
          <Typography variant='caption' gutterBottom>
            Last Updated : {formatDistance(timestamp, new Date())}
          </Typography>
          <Typography variant='body1' gutterBottom>
            Ch{lastChapter.no}&nbsp;:&nbsp;
            <span
              className={`
                ${lastChapter.name.length > 30 ? 'line-fade' : ''}
              `}>
              {lastChapter.name.substr(0, 30)}
            </span>
          </Typography>
          {
            comic.history
              ? (
                <div>
                  <Typography variant='caption' gutterBottom>
                    Last Watch
                  </Typography>
                  <Typography variant='body1' gutterBottom>
                    Chapter {comic.history.chapterNo}
                  </Typography>
                </div>
              )
              : null
          }
        </div>
      </Card>
    </div>
  )
}

ListCard.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
}

export default withStyles(styles, { withTheme: true })(withRouter(ListCard))



