import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'

import AppComicList from '../Common/AppComicList/AppComicList'

const styles = () => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  }
})

class HistoryContent extends Component {
  onToggleFavorite = (event, comic) => {
    event.stopPropagation()

    const {
      updateComicItem,
      getComicHistoryList,
      toggleLocalFavorite
    } = this.props

    comic.isFavorite = !comic.isFavorite

    updateComicItem(comic)
    setTimeout(() => getComicHistoryList(), 1000)
    toggleLocalFavorite(comic)
  }

  render () {
    const {
      classes,
      isDataLoaded,
      comicHistoryList,
      setSelectComic
    } = this.props

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              !isDataLoaded &&
              <div className={classes.loader}>
                <CircularProgress size={80} />
              </div>
            }
            {
              isDataLoaded &&
              <AppComicList
                comicList={comicHistoryList}
                setSelectComic={setSelectComic}
                onToggleFavorite={this.onToggleFavorite} />
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(HistoryContent)



