import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'

import Fade from '@material-ui/core/Fade'
import Paper from '@material-ui/core/Paper'

import ListCard from './ListCard/ListCard'

const styles = theme => ({
  root: {
    [theme.breakpoints.only('xs')]: {
      margin: '0 auto',
      width: 'calc(100% - 2rem)'
    }
  },
  card: {
    margin: '.5rem 0'
  }
})

const AppComicList = (props) => {
  const { classes, comicList, onToggleFavorite, setSelectComic } = props

  return (
    <Fade in>
      <div className={classes.root}>
        {comicList.map((comic, index) => (
          <Paper className={classes.card} key={index}>
            <ListCard
              comic={comic}
              setSelectComic={setSelectComic}
              onToggleFavorite={onToggleFavorite} />
          </Paper>
        ))}
      </div>
    </Fade>
  )
}

AppComicList.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles, { withTheme: true })(AppComicList)



