import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import HistoryContent from '../component/History/HistoryContent'

class History extends Component {
  componentDidMount () {
    const {setDataLoaded, getComicHistoryList} = this.props

    setDataLoaded(false)
    getComicHistoryList()
    setTimeout(() => setDataLoaded(true), 1000)
  }

  render () {
    return (
      <main>
        <HistoryContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    comicHistoryList: state.comicHistoryList,
    isDataLoaded: state.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getComicHistoryList: () => dispatch(actionCreators.getComicHistoryList()),
    updateComicItem: (comic) => dispatch(actionCreators.updateComicItem(comic)),
    toggleLocalFavorite: (comic) => dispatch(actionCreators.toggleLocalFavorite(comic)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    setSelectComic: (selectComic) => dispatch(actionCreators.setSelectComic(selectComic))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(History)



